.sidebar {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 220px;
  background: #000;
  color: #fff;
  position: relative;
}

.sidebar-settings {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 18px 0 18px 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  background: none;
}

.settings-btn {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.08em;
  font-weight: 900;
  font-family: 'Inter', 'Montserrat', 'Segoe UI', Arial, sans-serif;
  cursor: pointer;
  padding: 8px 24px;
  border-radius: 8px 8px 0 0;
  transition: background 0.15s, color 0.15s;
  display: flex;
  align-items: center;
  gap: 8px;
}
.settings-btn:hover, .settings-btn:focus {
  background: #23272f;
  color: #2e7dff;
}

.sidebar-header {
  font-family: 'Inter', 'Montserrat', 'Segoe UI', Arial, sans-serif;
  font-weight: 900;
  font-size: 1.35em;
  letter-spacing: 0.04em;
  padding: 32px 0 18px 32px;
  color: #fff;
}
.sidebar-nav ul {
  list-style: none;
  padding: 0 0 0 32px;
  margin: 0;
}
.sidebar-nav ul li {
  font-family: 'Inter', 'Montserrat', 'Segoe UI', Arial, sans-serif;
  font-weight: 900;
  font-size: 1.08em;
  letter-spacing: 0.03em;
  color: #fff;
  margin-bottom: 18px;
  cursor: pointer;
  transition: color 0.15s;
}
.sidebar-nav ul li:hover, .sidebar-nav ul li:focus {
  color: #2e7dff;
} 