/* --- Chat Window Layout --- */
.chat-window {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #181c20;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 16px rgba(0,0,0,0.10);
}

/* --- Header --- */
.chat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 24px;
  border-bottom: 1px solid #23272f;
  background: #20242b;
}

.chat-header-contact-name {
  font-size: 1.5em;
  font-weight: bold;
  color: #1f2937;
  flex: 1;
}

.chat-header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.chat-header-icons {
  display: flex;
  gap: 8px;
}
.chat-header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  padding: 8px;
  cursor: pointer;
  color: #6b7280;
  background: #f3f4f6;
  transition: background 0.15s, color 0.15s;
  border: none;
  width: 36px;
  height: 36px;
}
.chat-header-icon.active {
  color: #fff;
  background: #2e7dff;
}
.chat-header-icon:hover {
  background: #e5e7eb;
}
.chat-header-icon.active:hover {
  background: #1e40af;
}
.chat-header-contact {
  font-size: 1.08em;
  color: #fff;
  font-weight: 600;
}
.show-profile-btn {
  background: none !important;
  border: none !important;
  padding: 6px 10px !important;
  border-radius: 6px !important;
  cursor: pointer !important;
  font-size: 14px !important;
  color: #2e7dff !important;
  transition: background 0.15s !important;
  display: flex !important;
  align-items: center !important;
  gap: 4px !important;
}
.show-profile-btn:hover, .show-profile-btn:focus {
  background: #23272f !important;
}
.show-profile-btn span {
  color: #2e7dff !important;
  font-weight: 500 !important;
  font-size: 13px !important;
}

/* --- Messages --- */
.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 24px 16px 16px 16px;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

/* --- SMS Bubbles --- */
.chat-message.podium {
  align-self: flex-end;
  background: linear-gradient(90deg, #2563eb 0%, #1e40af 100%);
  color: #fff;
  border-radius: 18px 18px 4px 18px;
  padding: 14px 18px;
  max-width: 70%;
  font-size: 1rem;
  box-shadow: 0 2px 8px rgba(37,99,235,0.08);
  position: relative;
}
.chat-message.user {
  align-self: flex-start;
  background: #f3f4f6; /* lighter grey */
  color: #23272f;      /* dark grey text for contrast */
  border-radius: 18px 18px 18px 4px;
  padding: 14px 18px;
  max-width: 70%;
  font-size: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: relative;
}

/* --- Email Bubbles --- */
.email-bubble {
  background: #fff;
  color: #181c20;
  border: 2px solid #222c;
  border-radius: 16px;
  padding: 0 0 0 0;
  margin-bottom: 8px;
  max-width: 80%;
  align-self: flex-end;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: relative;
}
.email-customer {
  align-self: flex-start;
}
.email-header {
  padding: 12px 18px 0 18px;
  border-bottom: 1px solid #e5e7eb;
  font-size: 0.98em;
  color: #4b5563;
}
.email-subject {
  margin-top: 2px;
  font-weight: 600;
  color: #222;
}
.email-body {
  padding: 12px 18px 0 18px;
  font-size: 1.04em;
  color: #23272f;
}
.email-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 18px 10px 18px;
  border-top: 1px solid #e5e7eb;
}
.email-actions {
  display: flex;
  gap: 10px;
}
.email-action-btn {
  background: none;
  border: none;
  color: #2563eb;
  cursor: pointer;
  font-weight: 600;
  padding: 2px 8px;
  border-radius: 6px;
  transition: background 0.15s;
}
.email-action-btn:hover {
  background: #e0e7ff;
}

/* --- Phone Call Bubbles --- */
.phone-bubble {
  background: linear-gradient(90deg, #f3f4f6 0%, #dbeafe 100%);
  color: #222;
  border: 2px solid #b6c3d6;
  border-radius: 16px;
  padding: 0 0 0 0;
  margin-bottom: 8px;
  max-width: 80%;
  align-self: flex-end;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: relative;
}
.phone-customer {
  align-self: flex-start;
}
.phone-header {
  padding: 12px 18px 0 18px;
  border-bottom: 1px solid #e5e7eb;
  font-size: 0.98em;
  color: #64748b;
}
.phone-info {
  display: flex;
  gap: 18px;
  flex-wrap: wrap;
}
.phone-playback-section {
  padding: 10px 18px 0 18px;
}
.playback-controls {
  display: flex;
  align-items: center;
  gap: 8px;
}
.play-pause-button {
  background: none;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.15s;
}
.play-pause-button.playing {
  background: #ffe5e5;
}
.play-label {
  font-size: 0.98em;
  color: #2563eb;
  font-weight: 500;
}
.timeline-container {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 6px;
}
.timeline-track {
  flex: 1;
  height: 5px;
  background: #cbd5e1;
  border-radius: 2.5px;
  position: relative;
  cursor: pointer;
}
.timeline-progress {
  height: 100%;
  background: #2563eb;
  border-radius: 2.5px;
  position: absolute;
  left: 0;
  top: 0;
}
.timeline-handle {
  position: absolute;
  top: -4px;
  width: 13px;
  height: 13px;
  background: #2563eb;
  border-radius: 50%;
  border: 2px solid #fff;
  box-shadow: 0 1px 4px rgba(37,99,235,0.10);
  transform: translateX(-50%);
}
.timeline-time {
  font-size: 0.92em;
  color: #64748b;
  min-width: 60px;
  text-align: right;
}
.phone-summary {
  margin-top: 8px;
  font-size: 0.98em;
  color: #334155;
  background: #f1f5f9;
  border-radius: 8px;
  padding: 8px 10px;
}
.phone-transcript {
  margin-top: 6px;
  font-size: 0.95em;
  color: #475569;
  background: #f8fafc;
  border-radius: 8px;
  padding: 8px 10px;
  max-height: 120px;
  overflow-y: auto;
}
.transcript-header {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
}
.expand-btn {
  background: none;
  border: none;
  color: #2563eb;
  cursor: pointer;
  font-size: 0.98em;
  margin-left: 8px;
  border-radius: 6px;
  padding: 2px 8px;
  transition: background 0.15s;
}
.expand-btn:hover {
  background: #e0e7ff;
}
.phone-footer {
  padding: 8px 18px 10px 18px;
  border-top: 1px solid #e5e7eb;
  text-align: right;
}

/* --- Channel Selector & Dropdown --- */
.composer-channel-selector {
  padding: 0 24px 0 24px;
  margin-top: 8px;
  margin-bottom: 0;
  position: relative;
}
.channel-selector {
  display: flex;
  align-items: center;
  gap: 12px;
  background: #fff; /* white background */
  border-radius: 8px;
  padding: 8px 14px;
  cursor: pointer;
  font-size: 1.02em;
  color: #23272f;
  border: 1.5px solid #ececec;
  transition: border 0.15s, background 0.15s;
  position: relative;
  width: auto;
  min-width: 0;
  max-width: 260px;
}
.channel-selector:hover, .channel-selector:focus {
  border: 1.5px solid #2563eb;
  background: #f5faff;
}
.channel-label {
  font-weight: 600;
  color: #23272f;
}
.channel-contact {
  color: #6b7280;
  font-size: 0.98em;
}
.dropdown-arrow {
  margin-left: 8px;
  color: #6b7280;
}
.channel-dropdown {
  position: absolute;
  bottom: 110%;
  left: 0;
  background: #fff; /* white background */
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  z-index: 10;
  padding: 6px 0;
  border: 1.5px solid #2563eb;
  width: auto;
  min-width: 0;
  max-width: 260px;
}
.dropdown-option {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2px;
  padding: 10px 18px;
  cursor: pointer;
  color: #23272f;
  font-size: 1.01em;
  border-radius: 6px;
  transition: background 0.15s, color 0.15s;
}
.dropdown-option.selected, .dropdown-option:hover {
  background: #e0e7ff;
  color: #2563eb;
}
.contact-info {
  font-size: 0.95em;
  color: #6b7280;
}

/* --- Composer & Toolbar --- */
.chat-input {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 16px 36px 16px 24px;
  background: #fff !important;
  border-top: 1.5px solid #ececec !important;
}
.composer-toolbar {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-bottom: 4px;
}
.chat-input-textarea {
  width: 100%;
  min-height: 38px;
  max-height: 50vh;
  border-radius: 8px;
  border: 1.5px solid #222c;
  padding: 8px 12px;
  font-size: 1rem;
  background: #fff !important;
  color: #23272f !important;
  resize: vertical;
  box-shadow: 0 1px 2px rgba(0,0,0,0.03);
  overflow-y: auto;
}

.chat-input button[type="submit"] {
  align-self: flex-end;
  background: #2563eb;
  color: #fff;
  border: none;
  border-radius: 8px;
  width: auto;
  height: 38px;
  min-width: 72px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.05em;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(37,99,235,0.08);
  transition: background 0.15s;
}
.chat-input button[type="submit"]:hover {
  background: #1e40af;
}

/* --- Email Composer --- */
.email-composer {
  background: #fff;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 8px;
}
.email-fields {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.email-field {
  display: flex;
  align-items: center;
  gap: 8px;
}
.email-field label {
  font-weight: 600;
  color: #374151;
  min-width: 40px;
}
.email-fields-row {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}
.email-field input,
.email-field textarea {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  background: #fff;
}
.email-field input:disabled,
.email-field textarea:disabled {
  background: #f9fafb;
  color: #6b7280;
}
.email-subject-row {
  margin-top: 8px;
}
.email-subject-row input {
  font-weight: 500;
}
.email-formatting-toolbar {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}
.format-btn {
  background: none;
  border: none;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  color: #374151;
  transition: background 0.15s;
}
.format-btn:hover, .format-btn:focus {
  background: #f3f4f6;
}
.format-separator {
  width: 1px;
  height: 20px;
  background: #d1d5db;
  margin: 0 4px;
}
.email-formatting-toolbar .format-btn {
  background: none;
  border: none;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  color: #374151;
  transition: background 0.15s;
}
.email-formatting-toolbar .format-btn:hover, .email-formatting-toolbar .format-btn:focus {
  background: #f3f4f6;
}
.inventory-btn {
  background: none !important;
  border: none !important;
  padding: 6px 10px !important;
  border-radius: 4px !important;
  cursor: pointer !important;
  font-size: 14px !important;
  color: #374151 !important;
  transition: background 0.15s !important;
}
.inventory-btn:hover, .inventory-btn:focus {
  background: #f3f4f6 !important;
}
.inventory-btn span {
  color: #2e7dff !important;
  font-weight: 700 !important;
  font-size: 1.1em !important;
}
.utility-btn {
  background: none !important;
  border: none !important;
  padding: 6px 10px !important;
  border-radius: 4px !important;
  cursor: pointer !important;
  font-size: 14px !important;
  color: #374151 !important;
  transition: background 0.15s !important;
}
.utility-btn:hover, .utility-btn:focus {
  background: #f3f4f6 !important;
}
.utility-btn span {
  color: #374151 !important;
  font-weight: 500 !important;
  font-size: 1em !important;
}
.utility-btn.ai-recap-btn span {
  color: #2e7dff !important;
  font-weight: 600 !important;
}

/* --- Misc --- */
.chat-sender {
  font-weight: 500;
  margin-right: 8px;
}
.chat-timestamp {
  color: #a3a3a3;
  font-size: 0.9em;
  margin-left: 12px;
}
.chat-channel {
  color: #2563eb;
  font-weight: 600;
  margin-left: 4px;
}

/* --- Responsive --- */
@media (max-width: 600px) {
  .chat-window {
    border-radius: 0;
  }
  .chat-header, .chat-messages, .chat-input, .composer-channel-selector {
    padding-left: 6px;
    padding-right: 6px;
  }
  .chat-messages {
    padding-top: 12px;
    padding-bottom: 8px;
  }
  .composer-channel-selector {
    padding: 0 6px;
  }
} 

/* --- Force SMS composer icons to always be white with dark content --- */
.composer-toolbar .format-btn,
.composer-toolbar .format-btn:active,
.composer-toolbar .format-btn:visited {
  background: #fff !important;
  color: #23272f !important;
  box-shadow: none !important;
  border: none !important;
  outline: none !important;
} 

.sms-composer-input-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
}
.sms-textarea-with-icons {
  padding-right: 90px !important;
}
.sms-textarea-bottom-icons {
  min-height: 60px;
}
.sms-composer-icons-bottom {
  display: flex;
  gap: 6px;
  margin-top: 6px;
  margin-left: 2px;
  align-items: center;
}
.sms-composer-icons-bottom .format-btn {
  background: #fff !important;
  color: #23272f !important;
  border: none;
  font-size: 1.1em;
  cursor: pointer;
  border-radius: 6px;
  padding: 3px 5px;
  transition: background 0.15s, color 0.15s;
  box-shadow: none;
}
.sms-composer-icons-bottom .format-btn:hover, .sms-composer-icons-bottom .format-btn:focus {
  color: #2563eb !important;
  background: #e0e7ff !important;
} 

.sms-composer-textarea-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  align-items: stretch;
}
.sms-textarea-with-inside-icons {
  padding-bottom: 36px !important;
  padding-left: 60px !important;
}
.sms-composer-icons-inside {
  position: absolute;
  left: 8px;
  bottom: 8px;
  display: flex;
  gap: 6px;
  align-items: center;
  z-index: 2;
}

/* Enhanced tooltips for SMS composer icons */
.sms-icon-btn {
  position: relative;
}

.sms-icon-btn:hover::after {
  content: attr(title);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: #1f2937;
  color: #fff;
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  z-index: 1000;
  margin-bottom: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.15);
  pointer-events: none;
  opacity: 0;
  animation: tooltipFadeIn 0.2s ease-out forwards;
}

.sms-icon-btn:hover::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #1f2937;
  z-index: 1000;
  pointer-events: none;
  opacity: 0;
  animation: tooltipFadeIn 0.2s ease-out forwards;
}

@keyframes tooltipFadeIn {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-2px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}
