  background: #fff !important;
  color: #23272f !important;
  border: none;
  font-size: 1.1em;
  cursor: pointer;
  border-radius: 6px;
  padding: 3px 5px;
  transition: background 0.15s, color 0.15s;
  box-shadow: none;
}
.sms-composer-icons-inside .format-btn:hover, .sms-composer-icons-inside .format-btn:focus {
  color: #2563eb !important;
  background: #e0e7ff !important;
} 

.chat-input-html-editor {
  direction: ltr !important;
  text-align: left !important;
} 