.conversation-list {
  width: 320px;
  background: #f5f6fa;
  border-right: 1px solid #e0e0e0;
  overflow-y: auto;
  padding-top: 0;
}

.conversation-list-header {
  font-size: 1.15em;
  font-weight: bold;
  padding: 18px 20px 10px 20px;
  border-bottom: 1px solid #e0e0e0;
  background: #f5f6fa;
  letter-spacing: 0.5px;
}

.conversation-item {
  padding: 12px 20px;
  border-bottom: 1px solid #ececec;
  cursor: pointer;
  background: #f5f6fa;
  transition: background 0.2s;
  position: relative;
}
.conversation-item.selected {
  background: #e6eaff;
}
.conversation-name {
  font-weight: 500;
  margin-bottom: 2px;
}
.conversation-last {
  font-size: 0.95em;
  color: #888;
}
.conversation-unread {
  color: #2e7dff;
  font-size: 1.2em;
  position: absolute;
  right: 16px;
  top: 18px;
} 