.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-layout {
  display: flex;
  height: 100vh;
  font-family: 'Segoe UI', Arial, sans-serif;
}

.sidebar {
  width: 220px;
  background: #000 !important;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 0 16px 0;
  height: 100vh;
}

.sidebar-nav-top {
  width: 100%;
  margin-top: 0;
}

.sidebar-header {
  font-size: 1.3rem;
  font-weight: bold;
  padding: 24px 16px 12px 16px;
  border-bottom: 1px solid #333;
  width: 100%;
}

.sidebar-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.sidebar-nav li {
  padding: 12px 16px;
  cursor: pointer;
  transition: background 0.2s;
}
.sidebar-nav li:hover {
  background: #2c313a;
}
.sidebar-status {
  padding: 16px;
  border-top: 1px solid #333;
  font-size: 0.95rem;
}

.conversation-list {
  width: 320px;
  background: #f5f6fa;
  border-right: 1px solid #e0e0e0;
  overflow-y: auto;
  padding-top: 16px;
}

.conversation-item {
  padding: 12px 20px;
  border-bottom: 1px solid #ececec;
  cursor: pointer;
  background: #f5f6fa;
  transition: background 0.2s;
  position: relative;
}
.conversation-item.selected {
  background: #e6eaff;
}
.conversation-name {
  font-weight: 500;
  margin-bottom: 2px;
}
.conversation-last {
  font-size: 0.95em;
  color: #888;
}
.conversation-unread {
  color: #2e7dff;
  font-size: 1.2em;
  position: absolute;
  right: 16px;
  top: 18px;
}

.chat-window {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #fff;
}
.chat-header {
  padding: 18px 24px;
  border-bottom: 1px solid #ececec;
  font-size: 1.1rem;
  font-weight: 500;
  background: #f9fafd;
}
.chat-messages {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
}
.chat-message {
  margin-bottom: 18px;
  font-size: 1.05em;
}
.chat-sender {
  font-weight: 500;
  margin-right: 8px;
}
.chat-timestamp {
  color: #aaa;
  font-size: 0.9em;
  margin-left: 12px;
}
