} */
.chat-input input {
  flex: 1;
  padding: 10px 12px;
  border: 1px solid #d0d0d0;
  border-radius: 4px;
  font-size: 1em;
  margin-right: 12px;
}
.chat-input button {
  padding: 10px 20px;
  background: #2e7dff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 1em;
  cursor: not-allowed;
  opacity: 0.7;
}

.chip-btn {
  border: none;
  outline: none;
  border-radius: 999px;
  padding: 6px 18px;
  font-size: 1em;
  font-weight: 500;
  background: #f3f4f6;
  color: #2563eb;
  box-shadow: 0 1px 4px rgba(0,0,0,0.06);
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
}
.chip-btn.open {
  background: #2563eb;
  color: #fff;
}
.chip-btn:hover {
  background: #e0e7ff;
  color: #2563eb;
}
